"use client"

import { useEffect, useState } from "react"
import { useParams, useRouter } from "next/navigation"
import { useContract } from "@/context/contract-context"
import { useSubgraph } from "@/context/graphql/queries"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Skeleton } from "@/components/ui/skeleton"
import { Input } from "@/components/ui/input"
import { toast } from "sonner"
import { formatTimestamp, getStatusText, getRestrictionText, getVotingTypeText, getResultTypeText } from "@/lib/utils"
import CampaignResults from "./campaign-results"
import CandidateList from "./candidate-list"
import ProposalList from "./proposal-list"
import CountdownTimer from "./countdown-timer"
import DashboardStats from "./dashboard-stats"
import { motion } from "framer-motion"
import { ArrowLeft } from "lucide-react"
import Link from "next/link"

export default function CampaignDetailsPage() {
  const params = useParams()
  const router = useRouter()
  const campaignId = params.id ? Number(params.id) : 0
  const { contract, address, isConnected } = useContract()
  const { events, loading: subgraphLoading } = useSubgraph()

  // Campaign data states
  const [loading, setLoading] = useState(true)
  const [campaignInfo, setCampaignInfo] = useState(null)
  const [campaignStats, setCampaignStats] = useState(null)
  const [campaignTimeInfo, setCampaignTimeInfo] = useState(null)
  const [userRegistrationType, setUserRegistrationType] = useState(null)
  const [hasVoted, setHasVoted] = useState(false)
  const [activeTab, setActiveTab] = useState("info")
  const [passKey, setPassKey] = useState("")

  // Fetch campaign data
  useEffect(() => {
    if (!contract || !campaignId) return

    const fetchCampaignData = async () => {
      try {
        setLoading(true)

        // Fetch basic campaign info
        const info = await contract.getCampaignBasicInfo(campaignId)
        setCampaignInfo({
          votingType: Number(info.votingType),
          restriction: Number(info.restriction),
          resultType: Number(info.resultType),
          creator: info.creator,
          description: info.description,
        })

        // Fetch campaign stats
        const stats = await contract.getCampaignStats(campaignId)
        setCampaignStats({
          totalVotes: Number(stats.totalVotes),
          itemCount: Number(stats.itemCount),
          registeredVoterCount: Number(stats.registeredVoterCount),
        })

        // Fetch campaign time info
        const timeInfo = await contract.getCampaignTimeInfo(campaignId)
        setCampaignTimeInfo({
          status: Number(timeInfo.status),
          startTime: timeInfo.startTime,
          endTime: timeInfo.endTime,
        })
 
        // Check if user is registered and has voted
        if (isConnected && address) {
          const regType = await contract.getUserRegistrationType(campaignId, address)
          setUserRegistrationType(Number(regType))
       

          const voted = await contract.hasUserVoted(campaignId, address)
          setHasVoted(voted)
        }

        // Set initial active tab based on campaign status and user role
        setInitialActiveTab(Number(timeInfo.status))
      } catch (error) {
        console.error("Error fetching campaign data:", error)
        toast.error("Failed to load campaign data")
      } finally {
        setLoading(false)
      }
    }

    fetchCampaignData()
  }, [contract, campaignId, address, isConnected])

  // Set initial active tab based on campaign status and user role
  const setInitialActiveTab = (status) => {
    if (!campaignInfo) return

    const isCreator = address && campaignInfo.creator.toLowerCase() === address.toLowerCase()
    const now = Math.floor(Date.now() / 1000)
    const startTime = campaignTimeInfo ? Number(campaignTimeInfo.startTime) : 0
    const endTime = campaignTimeInfo ? Number(campaignTimeInfo.endTime) : 0

    if (isCreator) {
      // Creator always sees results
      setActiveTab("results")
    } else if (status === 0) {
      // Before campaign starts
      if (campaignInfo.restriction !== 0) {
        // Not Open
        setActiveTab("register")
      } else {
        setActiveTab("info")
      }
    } else if (status === 1) {
      // During voting period
      if (userRegistrationType > 0 && !hasVoted) {
        setActiveTab("vote")
      } else if (hasVoted) {
        setActiveTab("results")
      } else {
        setActiveTab("info")
      }
    } else {
      // After campaign ends
      setActiveTab("results")
    }
  }

  // Check if user can vote
  const canVote = () => {
    if (!campaignTimeInfo || !isConnected) return false

    const now = Math.floor(Date.now() / 1000)
    const startTime = Number(campaignTimeInfo.startTime)
    const endTime = Number(campaignTimeInfo.endTime)

    return now >= startTime && now < endTime && userRegistrationType > 0 && !hasVoted
  }

  // Check if user can register
  const canRegister = () => {
    if (!campaignTimeInfo || !campaignInfo || !isConnected) return false

    const now = Math.floor(Date.now() / 1000)
    const startTime = Number(campaignTimeInfo.startTime)

    return (
      now < startTime &&
      campaignInfo.restriction !== 0 && // Not Open
      userRegistrationType === 0 // Not registered
    )
  }

  // Handle vote casting
  const handleVote = async (itemId) => {
    if (!contract || !isConnected) {
      toast.error("Please connect your wallet")
      return
    }

    try {
      setLoading(true)

      let tx
      if (campaignInfo.votingType === 0) {
        // Candidate-based
        tx = await contract.voteForCandidate(campaignId, itemId)
      } else {
        // Proposal-based
        tx = await contract.voteForProposal(campaignId, itemId)
      }

      await tx.wait()
      setHasVoted(true)
      toast.success("Vote cast successfully!")

      // Refresh campaign stats
      const stats = await contract.getCampaignStats(campaignId)
      setCampaignStats({
        totalVotes: Number(stats.totalVotes),
        itemCount: Number(stats.itemCount),
        registeredVoterCount: Number(stats.registeredVoterCount),
      })

      setActiveTab("results")
    } catch (error) {
      console.error("Error casting vote:", error)
      toast.error("Failed to cast vote")
    } finally {
      setLoading(false)
    }
  }

  // Handle registration
  const handleRegister = async () => {
    if (!contract || !isConnected) {
      toast.error("Please connect your wallet")
      return
    }

    if (!passKey && campaignInfo.restriction === 2) {
      // Registration Required
      toast.error("Please enter the pass key")
      return
    }

    try {
      setLoading(true)

      const tx = await contract.registerToVote(campaignId, passKey)
      await tx.wait()

      const regType = await contract.getUserRegistrationType(campaignId, address)
      setUserRegistrationType(Number(regType))

      toast.success("Registration successful!")

      // Refresh campaign stats
      const stats = await contract.getCampaignStats(campaignId)
      setCampaignStats({
        totalVotes: Number(stats.totalVotes),
        itemCount: Number(stats.itemCount),
        registeredVoterCount: Number(stats.registeredVoterCount),
      })

      setActiveTab("info")
    } catch (error) {
      console.error("Error registering:", error)
      toast.error("Failed to register")
    } finally {
      setLoading(false)
    }
  }

  if (loading || !campaignInfo || !campaignTimeInfo) {
    return (
      <div className="container mx-auto py-8 px-4">
        <div className="space-y-6">
          <Skeleton className="h-10 w-40" />
          <Skeleton className="h-12 w-3/4" />
          <Skeleton className="h-6 w-1/2" />
          <Skeleton className="h-32 w-full" />
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
          </div>
          <Skeleton className="h-64 w-full" />
        </div>
      </div>
    )
  }

  const isCreator = address && campaignInfo.creator.toLowerCase() === address.toLowerCase()
  const now = Math.floor(Date.now() / 1000)
  const startTime = Number(campaignTimeInfo.startTime)
  const endTime = Number(campaignTimeInfo.endTime)

  // Determine countdown message
  let countdownMessage = ""
  let targetTime = 0

  if (now < startTime) {
    countdownMessage = "Voting starts in:"
    targetTime = startTime
  } else if (now < endTime) {
    countdownMessage = "Voting ends in:"
    targetTime = endTime
  } else {
    countdownMessage = "Campaign ended"
  }

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
      },
    },
  }

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 },
  }

  return (
    <div className="container mx-auto mt-14 py-8 px-4">
      <motion.div className="mb-8" initial="hidden" animate="visible" variants={containerVariants}>
        <motion.div variants={itemVariants} className="mb-4">
          <Link
            href="/campaigns"
            className="inline-flex items-center text-sm text-muted-foreground hover:text-foreground transition-colors mb-4"
          >
            <ArrowLeft className="h-4 w-4 mr-1" />
            Back to campaigns
          </Link>

          <h1 className="text-3xl font-bold mb-2">{campaignInfo.description}</h1>

          <div className="flex flex-wrap gap-2 mb-4">
            <Badge variant="outline" className={campaignTimeInfo.status === 1 ? "bg-primary/10 text-primary" : ""}>
              {getStatusText(campaignTimeInfo.status)}
            </Badge>
            <Badge variant="outline">{getVotingTypeText(campaignInfo.votingType)}</Badge>
            <Badge variant="outline">{getRestrictionText(campaignInfo.restriction)}</Badge>
            <Badge variant="outline">{getResultTypeText(campaignInfo.resultType)}</Badge>
          </div>
        </motion.div>

        <motion.div variants={itemVariants} className="bg-muted/30 p-6 rounded-lg border border-border mb-6">
          <h2 className="text-lg font-medium mb-4">{countdownMessage}</h2>
          {now < endTime ? <CountdownTimer targetTime={targetTime} /> : <p>Final results are available</p>}
        </motion.div>

        <motion.div variants={itemVariants} className="mb-8">
          <DashboardStats
            campaignStats={campaignStats}
            campaignInfo={campaignInfo}
            campaignTimeInfo={campaignTimeInfo}
          />
        </motion.div>
      </motion.div>

      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="grid w-full grid-cols-4">
          <TabsTrigger value="info">Info</TabsTrigger>
          {canRegister() && <TabsTrigger value="register">Register</TabsTrigger>}
          {canVote() && <TabsTrigger value="vote">Vote</TabsTrigger>}
          <TabsTrigger value="results">Results</TabsTrigger>
        </TabsList>

        <TabsContent value="info" className="mt-6">
          <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.3 }}>
            <Card>
              <CardHeader>
                <CardTitle>Campaign Information</CardTitle>
                <CardDescription>Created by {campaignInfo.creator}</CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                <div>
                  <h3 className="text-base font-medium mb-2">Time Frame</h3>
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 p-4 bg-muted/30 rounded-lg">
                    <div>
                      <p className="text-sm text-muted-foreground">Start Date</p>
                      <p className="font-medium">{formatTimestamp(campaignTimeInfo.startTime)}</p>
                    </div>
                    <div>
                      <p className="text-sm text-muted-foreground">End Date</p>
                      <p className="font-medium">{formatTimestamp(campaignTimeInfo.endTime)}</p>
                    </div>
                  </div>
                </div>

                {isConnected && (
                  <div className="p-4 bg-muted/30 rounded-lg">
                    <h3 className="text-base font-medium mb-2">Your Status</h3>
                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                      <div>
                        <p className="text-sm text-muted-foreground">Registration</p>
                        <p className="font-medium">
                          {userRegistrationType > 0
                            ? `Registered as: ${userRegistrationType === 1 ? "Voter" : "Candidate"}`
                            : "Not registered"}
                        </p>
                      </div>
                      <div>
                        <p className="text-sm text-muted-foreground">Voting</p>
                        <p className="font-medium">{hasVoted ? "You have voted" : "You have not voted yet"}</p>
                      </div>
                    </div>
                  </div>
                )}
              </CardContent>
              <CardFooter>
                {!isConnected && (
                  <div className="w-full p-4 bg-muted/30 rounded-lg text-center">
                    <p className="text-muted-foreground">Connect your wallet to participate in this campaign</p>
                  </div>
                )}
              </CardFooter>
            </Card>
          </motion.div>
        </TabsContent>

        {canRegister() && (
          <TabsContent value="register" className="mt-6">
            <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.3 }}>
              <Card>
                <CardHeader>
                  <CardTitle>Register to Vote</CardTitle>
                  <CardDescription>Register to participate in this campaign</CardDescription>
                </CardHeader>
                <CardContent>
                  {campaignInfo.restriction === 2 && (
                    <div className="mb-6">
                      <label className="block text-sm font-medium mb-2">Pass Key</label>
                      <Input
                        type="text"
                        placeholder="Enter the required pass key"
                        value={passKey}
                        onChange={(e) => setPassKey(e.target.value)}
                        className="w-full"
                      />
                    </div>
                  )}
                  <div className="p-4 bg-muted/30 rounded-lg mb-4">
                    <h3 className="text-sm font-medium mb-2">About Registration</h3>
                    <ul className="text-sm text-muted-foreground space-y-2">
                      <li>• By registering, you will receive an NFT representing your voting right</li>
                      <li>• This NFT proves your eligibility to participate in this campaign</li>
                      <li>• Registration must be completed before voting begins</li>
                    </ul>
                  </div>
                </CardContent>
                <CardFooter>
                  <Button onClick={handleRegister} disabled={loading} className="w-full" size="lg">
                    {loading ? "Registering..." : "Register for Campaign"}
                  </Button>
                </CardFooter>
              </Card>
            </motion.div>
          </TabsContent>
        )}

        {canVote() && (
          <TabsContent value="vote" className="mt-6">
            <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.3 }}>
              <Card>
                <CardHeader>
                  <CardTitle>Cast Your Vote</CardTitle>
                  <CardDescription>
                    Select an option to vote. Your choice is final and cannot be changed.
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  {campaignInfo.votingType === 0 ? (
                    <CandidateList campaignId={campaignId} contract={contract} onVote={handleVote} loading={loading} />
                  ) : (
                    <ProposalList campaignId={campaignId} contract={contract} onVote={handleVote} loading={loading} />
                  )}
                </CardContent>
              </Card>
            </motion.div>
          </TabsContent>
        )}

        <TabsContent value="results" className="mt-6">
          <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.3 }}>
            <Card>
              <CardHeader>
                <CardTitle>Results Dashboard</CardTitle>
                <CardDescription>
                  {campaignTimeInfo.status === 2 ? "Final results" : "Current standings"}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <CampaignResults
                  campaignId={campaignId}
                  contract={contract}
                  votingType={campaignInfo.votingType}
                  status={campaignTimeInfo.status}
                />
              </CardContent>
            </Card>
          </motion.div>
        </TabsContent>
      </Tabs>
    </div>
  )
}
