"use client"

import { useEffect, useState } from "react"
import { getTimeRemaining } from "@/lib/utils"
import { motion } from "framer-motion"

export default function CountdownTimer({ targetTime }) {
  const [timeLeft, setTimeLeft] = useState({ days: 0, hours: 0, minutes: 0, seconds: 0 })
  const [isExpired, setIsExpired] = useState(false)

  useEffect(() => {
    const updateTimer = () => {
      const remaining = getTimeRemaining(targetTime)
      setTimeLeft(remaining)

      // Check if timer has expired
      const total = Number(targetTime) * 1000 - Date.now()
      if (total <= 0) {
        setIsExpired(true)
      } else {
        setIsExpired(false)
      }
    }

    // Update immediately
    updateTimer()

    // Then update every second
    const interval = setInterval(updateTimer, 1000)

    return () => clearInterval(interval)
  }, [targetTime])

  if (isExpired) {
    return (
      <div className="text-center p-4 bg-muted/50 rounded-lg border border-muted animate-pulse">
        <p className="text-lg font-medium">Time expired</p>
      </div>
    )
  }

  const timeBoxVariants = {
    initial: { scale: 0.8, opacity: 0 },
    animate: { scale: 1, opacity: 1, transition: { duration: 0.3 } },
    pulse: {
      scale: [1, 1.05, 1],
      transition: { duration: 1, repeat: Number.POSITIVE_INFINITY, repeatType: "loop" },
    },
  }

  return (
    <div className="flex gap-4 justify-center md:justify-start py-2">
      {[
        { value: timeLeft.days, label: "Days" },
        { value: timeLeft.hours, label: "Hours" },
        { value: timeLeft.minutes, label: "Minutes" },
        { value: timeLeft.seconds, label: "Seconds" },
      ].map((item, index) => (
        <motion.div
          key={item.label}
          className="text-center"
          initial="initial"
          animate="animate"
          variants={timeBoxVariants}
          transition={{ delay: index * 0.1 }}
        >
          <motion.div
            className="bg-card w-16 h-16 rounded-lg flex items-center justify-center border border-border shadow-sm"
            variants={item.value <= 0 ? timeBoxVariants : {}}
            animate={item.value <= 0 ? "pulse" : "animate"}
          >
            <span className="text-3xl font-bold">{item.value}</span>
          </motion.div>
          <div className="text-xs uppercase mt-1 text-muted-foreground">{item.label}</div>
        </motion.div>
      ))}
    </div>
  )
}
